@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #f9f9f9;
}
  
.navbar-container {
    display: flex;
    background-color: #000000;
    justify-content: space-around;
    align-items: center;
    height: 15vh;
}


.navbar-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: #68e1fd;
}


ul {
    align-items: center;
    display: flex;
    list-style: none;
}


li {
    transition: transform 0.5s;
}

li:hover {
    transform: translateY(-3px);
}

.link {
    margin-right: 5rem;
    font-size: 1.5rem;
    font-weight: 500;
    text-decoration: none;
    color: #f9f9f9;
}

.cart-logo-border {
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.8rem;
    width: 50px;
    height: 50px;
    background-color: #f9f9f9;
}


.cart-length {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    position: absolute;
    left: -10px;
    bottom: -10px;
    background-color: #68e1fd;
    color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    font-weight: 700;
}

.link:hover {
    color: #68e1fd;
}



@media screen and (max-width: 600px) {

    .navbar-container {
        display: flex;
        flex-direction: column;
        background-color: #000000;
        height: 25vh;
    }
    
    
    .navbar-title {
        font-size: 2.5rem;
        font-weight: 700;
    }
    
    
    ul {
        display: flex;
        list-style: none;
        justify-content: center;
    }
    
    
    
    .link {
        margin-right: 1.5rem;
    }
    
  }






